.main-container {
  position: absolute;
  top: 5%;
  left: 5%;
  width: 90%;
  height: 90vh;
  background-color: #9ACEEB;
  border: 1px solid black;
  border-radius: 15px;
  opacity: 0.5;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.cheking-my-cow {
  width: 40%;
  min-height: 40%; 
  padding: 10px;
  margin: 20px;
  border-radius: 15px;
  background-color: #6c95d3;
  border: 1px solid black;
  flex-grow: 1;
}

.cheking-enemy-cow {
  width: 40%;
  min-height: 40%; 
  padding: 10px;
  margin: 20px;
  border-radius: 15px;
  background-color: #6c95d3;
  border: 1px solid black;
  flex-grow: 1;
}

.input-my-number {
  text-align: center;
  width: calc(5% + 15px);
  height: 40px;
  border-radius: 5px;
  margin: 1px;
  font-size: 24px;
  padding-left: 9px;
  margin-bottom: 20px;
  font-weight: 700;
}

.input-checked-number {
  text-align: center;
  width: calc(5% + 10px);
  height: 30px;
  border-radius: 5px;
  font-size: 24px;
  margin: 1px;
  font-weight: 700;
}

.result-cheked-number {
  display: inline;
  text-align: center;
  width: calc(4% + 5px);
  height: 30px;
  font-size: 30px;
  align-self: flex-end;
  font-weight: 700;
}

.cow-number,
.bull-number {
  margin-left: 4px;
  margin-right: 3px;
  text-align: center;
  width: calc(4% + 5px);
  height: 30px;
  border-radius: 5px;
  font-size: 24px;
  font-weight: 700;
}

.cheking-enemy-cow {
  font-size: 30px;
  font-weight: 700;
}

.cheking-enemy-cow .input-checked-number,
.cheking-enemy-cow .cow-number,
.cheking-enemy-cow .bull-number,
.cheking-enemy-cow .input-crossOut-number {
  text-align: center;
  width: calc(4% + 5px);
  height: 30px;
  border-radius: 5px;
  font-size: 24px;
  margin: 1px;
  padding-left: 9px;
  margin-bottom: 10px;
  font-weight: 700;
}


.cheking-enemy-cow .cow-number,
.cheking-enemy-cow .bull-number {
  width: 8%;
  margin-left: 4px;
  margin-top: 3px;
  opacity: 0.3;
  padding-left: 0px;
}

.container-cheked-number {
  display: inline;
  width: calc(2% + 5px);
  height: 30px;
  margin-bottom: 10px;
  margin-top: 0px;
  position: relative;
}

.crossOut-number {
  background: linear-gradient(135deg, rgba(255,255,255,1) 45%, rgba(255,0,0,1) 46%, rgba(251,0,0,1) 52%, rgba(255,255,255,1) 54%);
  -webkit-text-stroke: 0.2px white;
}